*,
*::before,
*::after {
  box-sizing: border-box; }

html,
body {
  margin: 0;
  padding: 0;
  min-height: 100vh; }

html {
  font-style: normal;
  font-weight: 300;
  font-size: 16px;
  line-height: 24px;
  font-family: "Placeholder", "Arial", sans-serif;
  color: #000000;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-rendering: optimizeLegibility; }

body {
  width: 100%;
  height: 100%;
  background-color: #ffffff; }

a {
  color: #000000;
  text-decoration: none; }

img,
video {
  display: block;
  max-width: 100%;
  height: auto; }

textarea {
  resize: none; }

input:-webkit-autofill {
  box-shadow: inset 0 0 0 1000px #ffffff;
  -webkit-text-fill-color: #000000; }

input,
textarea {
  border-radius: 0; }
  input::placeholder,
  textarea::placeholder {
    opacity: 1; }
  input:invalid,
  textarea:invalid {
    box-shadow: none; }

select {
  border-radius: 0; }

input::-ms-clear, input::-ms-reveal {
  display: none; }

input[type="search"]::-webkit-search-decoration,
input[type="search"]::-webkit-search-cancel-button,
input[type="search"]::-webkit-search-results-button,
input[type="search"]::-webkit-search-results-decoration {
  appearance: none; }

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  margin: 0;
  appearance: none; }

input[type="number"] {
  appearance: textfield; }

select,
textarea,
input:matches([type="email"],
[type="number"],
[type="password"],
[type="search"],
[type="tel"],
[type="text"],
[type="url"]) {
  appearance: none; }

button,
[type="button"],
[type="reset"],
[type="submit"] {
  appearance: none; }

.container {
  width: 100%;
  max-width: 1680px;
  padding: 10px 10px 0;
  margin: 0 auto; }
  @media screen and (max-width: 1680px) {
    .container {
      max-width: 1400px; } }
  @media screen and (max-width: 1490px) {
    .container {
      max-width: 1240px; } }
  @media screen and (max-width: 1340px) {
    .container {
      max-width: 1140px; } }
  @media screen and (max-width: 1220px) {
    .container {
      max-width: 960px; } }
  @media screen and (max-width: 992px) {
    .container {
      max-width: 730px; } }
  @media screen and (max-width: 767px) {
    .container {
      max-width: 100%; } }

.sections {
  margin-bottom: 20px; }
  .sections__list {
    list-style: none;
    margin: 0;
    padding: 0;
    display: flex;
    flex-wrap: wrap; }
  .sections__img {
    height: 15.625rem;
    margin-bottom: 0.625rem;
    display: flex;
    align-items: center;
    overflow: hidden;
    border-radius: 18px; }
    @media (hover: hover), screen and (min-width: 0\0) {
      .sections__img:hover img {
        transform: scale(1.1); } }
    @media (max-width: 1490px) {
      .sections__img {
        height: 14.0625rem; } }
    @media (max-width: 1340px) {
      .sections__img {
        height: 12.5rem; } }
    @media (max-width: 1220px) {
      .sections__img {
        height: 10.9375rem; } }
    @media (max-width: 767px) {
      .sections__img {
        height: auto; } }
    .sections__img img {
      border-radius: inherit;
      object-fit: cover;
      transition: transform 0.5s ease;
      height: 100%; }
  .sections__elem {
    width: 25%;
    padding: 0 1.25rem;
    margin-bottom: 1.25rem; }
    @media (max-width: 1680px) {
      .sections__elem {
        padding: 0 0.625rem; } }
    @media (max-width: 1220px) {
      .sections__elem {
        width: 33%; } }
    @media (max-width: 767px) {
      .sections__elem {
        width: 50%; } }
    @media (max-width: 580px) {
      .sections__elem {
        width: 100%; } }
  .sections__text {
    margin: 0.625rem 0 0 0;
    text-align: center; }
    @media (max-width: 767px) {
      .sections__text {
        font-size: 0.75rem; } }
  @media (max-width: 767px) {
    .sections__number {
      font-size: 0.75rem; } }
  .sections__info {
    display: flex;
    flex-direction: column;
    align-items: center; }
  .sections__title {
    text-align: center;
    margin-right: 0.25rem;
    text-transform: uppercase;
    color: #1a3c55;
    font-size: 1.2rem; }
    @media (max-width: 1220px) {
      .sections__title {
        font-size: 1.125rem; } }
    @media (max-width: 767px) {
      .sections__title {
        font-size: 1rem; } }

.content-info {
  margin-bottom: 80px;
  position: relative; }
  .content-info__list {
    display: flex;
    flex-wrap: wrap; }
  .content-info__item {
    flex: 0 0 50%;
    max-width: 50%; }
    @media (max-width: 990px) {
      .content-info__item {
        flex: 0 0 100%;
        max-width: 100%; }
        .content-info__item:not(:last-of-type) {
          margin-bottom: 1.25rem; } }
    .content-info__item:nth-child(odd) {
      padding-right: 2rem; }
      @media (max-width: 990px) {
        .content-info__item:nth-child(odd) {
          padding-right: 0; } }
    .content-info__item:nth-child(even) {
      padding-left: 2rem; }
      @media (max-width: 990px) {
        .content-info__item:nth-child(even) {
          padding-left: 0; } }
  .content-info__img {
    display: flex;
    justify-content: center; }
  .content-info__subtitle {
    padding-bottom: 0.6875rem;
    border-bottom: 1px solid #e6e6e6;
    margin: 0 0 0.9375rem;
    text-align: center;
    font-size: 1rem;
    font-weight: 400;
    text-transform: uppercase; }
  .content-info__description {
    font-size: 1.25rem;
    line-height: 1.4;
    margin-bottom: 1.25rem; }
  .content-info__title {
    font-size: 3rem;
    line-height: 3.625rem;
    margin: 0; }
  .content-info__cases {
    list-style: none;
    margin: 0;
    padding: 0; }
  .content-info__icon {
    width: 1.375rem;
    margin-right: 0.625rem;
    font-size: 1.375rem;
    color: #00adec; }
  .content-info__subcase {
    display: flex;
    align-items: center; }
    .content-info__subcase:not(:last-of-type) {
      margin-bottom: 0.625rem; }
  .content-info__case-text {
    margin: 0; }
  .content-info__advantages-img {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translateX(-50%) translateY(-50%);
    max-width: 16.875rem;
    max-height: 30.625rem; }
    @media (max-width: 990px) {
      .content-info__advantages-img {
        display: none; } }
  .content-info__advantages {
    list-style: none;
    margin: 0;
    padding: 0;
    display: grid;
